    methods:{
        randomSort(a, b) {
        const vm=this
        // // arrlength10
        // // pushnewArr
        // if(arr.length == 1) {
        // newArr.push(arr[0]);
        // return newArr; // 
        // }
        // // length
        // let random = Math.ceil(Math.random() * arr.length) - 1;
        // // pushnewArr
        // newArr.push(arr[random]);
        // // arr
        
        // return randomSort(arr, newArr);
        vm.shuffleCoupons= Math.random() > 0.5 ? -1 : 1;
        },
        shuffleCoupon(){
            const vm=this                        
            vm.coupons.sort(function(){
                vm.comparisonA=Math.random()
                vm.comparisonB=Math.random()
                if (vm.comparisonA>vm.comparisonB) {
                    return -1;
                }
                if (vm.comparisonA<vm.comparisonB) {
                    return 1;
                }
                // a must be equal to b
                return 0;
            })
        },
        getCoupon(){
            const vm=this
            for(let i=0; i<3; i++){
                let a=parseInt(Math.random()*7);
                vm.randomCoupons.push(vm.coupons[a])
            }
            console.log(vm.randomCoupons)
        },
        startDrawing(){
            $('.start-btn').fadeOut();
            $('.card-deck').fadeIn();

        }
        

        
    },
    mounted(){
        $(".card").click(function(e){
            $(e.currentTarget).addClass("flip");
            
            $(e.currentTarget).siblings().off() //
            
            
        });
        $('#start-modal').modal('show')
        $('body').click(function(e){
            $('#start-modal').modal('hide')
        })
        
    },
    created(){        
        this.shuffleCoupon()
        this.getCoupon()
        
        
    }
}
